body {
    background-color: #444;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-family: "Noto Serif JP";
    padding-top: 5%;
    word-wrap: break-word;
}

.logo {
    height: 200px;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.WalletInfo {
    width: 500px;
}

.Block {
    /* width: 800px; */
    border: 1px solid #fff;
    padding: 5%;
    margin: 2%;
}

.Transaction {
    padding: 5%;
}

.ConductTransaction, .TransactionPool {
    margin: 10%;
    /* max-width: 500px; */
}

a, a:hover {
    color: #e66;
    text-decoration: underline;
}
